.character {
  height: 48px;
  width: 30px;
  background-image: url("char2.f05c846a.png");
  background-size: cover;
  display: none;
  position: absolute;
  transform: translate(38%, -52%);
}

.character-animation {
  animation: .5s cubic-bezier(.81, .2, .18, 1.03) char-anim;
}

@keyframes char-anim {
  0% {
    height: 48px;
    width: 30px;
  }

  10% {
    height: 52px;
    width: 32px;
    transform: translate(38%, -52%);
  }

  20% {
    transform: translate(10%, -52%);
  }

  40% {
    transform: translate(60%, -52%);
  }

  60% {
    transform: translate(10%, -52%);
  }

  80% {
    transform: translate(60%, -52%);
  }

  90% {
    height: 52px;
    width: 32px;
    transform: translate(38%, -52%);
  }

  100% {
    height: 48px;
    width: 30px;
  }
}

